import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

const initialState = {};

@observer
class Tracks extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadTracks();
    }

	loadTracks() {
		const { trackStore } = this.props.stores;
		trackStore.load();
	}

	render() {
		const { trackStore, userStore } = this.props.stores;
		const { tracks } = trackStore;
		const { user } = userStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
				<div class='row'>
					<div class='col-12'>
						<h5>Treningsrundene dine - {tracks.length} stk</h5>
						<div class='row'>
							{tracks && tracks.map(track => (
								<div class={`col-6 col-sm-4 col-lg-2 mt-2 p-2`}>
									{/* {JSON.stringify(track, null, 4)} */}
									<div class='text-center border rounded p-2 mb-1'>
										<a href={`/tracks/${track.id}`} class='stretched-link'>
											{track.image ? <img src={track.image} class='img-fluid' /> : <i class='fas fa-map-marked text-muted' style='font-size: 4vw;' />}
										</a>
									</div>
									<div>
										<span class='float-right text-muted'>
											{track.distanceKm} km
										</span>
										{track.name} 
									</div>
									<small>
										<i class='fas fa-mountain text-muted' /> {track.elevation} m
									</small>
								</div>
							))}
						</div>

						<div class='col-12'>
							<a class='btn btn-primary mt-5 float-right'
								href='/tracks/new'
							>
								<i class='fas fa-plus' /> Legg til runde
							</a>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Tracks;
